import React, {useState} from "react";
import { MovieList, Search, Header, Footer,  FavouriteMovieList, Menu } from "./components";
// import Header from "./components/Header";
import themeDark from "utils/themeDark";
import themeLight from "utils/themeLight";
 
import { ThemeProvider } from "styled-components";
import GlobalStyles from "./index.css";
import { AppWrapper, Container, MovieListWrapper } from "./App.css";
import { CSSTransition, TransitionGroup } from "react-transition-group";

// Załozenia: propTypes, struktura, redux, normalize, CSSGrid, styledComponent
// Dodać routing
// Pobieranie danych przenieść do MovieList
// Wyszukiwanie przenieśc do Search
function App(props) {
  const [showFavourite, setShowFavourite] = useState(false);
  const [changeTheme, setChangeTheme] = useState(false);

  console.log(showFavourite);
  return (
    <>
      <ThemeProvider theme={!changeTheme ? themeDark : themeLight}>
        <GlobalStyles />
        <AppWrapper>
        <TransitionGroup>
            <CSSTransition
              appear={true}
              timeout={600}
              classNames="fade"
              key={showFavourite}
            >
          <Container>
            <Header />
            <Menu showFavourite={()=>setShowFavourite(!showFavourite)} changeTheme={()=>setChangeTheme(!changeTheme)}/>
            {showFavourite && <FavouriteMovieList showFavourite={()=>setShowFavourite(!showFavourite)}/>}
             <Search />
            <MovieList />
            <Footer />
          </Container>
          </CSSTransition></TransitionGroup>
        </AppWrapper>
      </ThemeProvider>
    </>
  );
}

export default App;
