import React from "react";
import { connect } from "react-redux";
import {
  Container,
  ButtonClose,
  MovieSelectedContainer,
  MoviePoster,
  MovieTitle,
  MovieRating,
  Star,
  MovieDetails,
  DetailKey,
  DetailValue,
  DescriptionHeader,
  DescriptionText,
  MovieDescriptionWrapper,
  MovieSelectedWrapper,
  AddFavouriteMovie,
  Icon
} from "./MovieSelected.css";
import { removeSelectedMovie, addFavouriteMovie, removeFavouriteMovie } from "data/actions";
import configureStore from "data/store";

 
const MovieSelected = ({ selectedMovie, favouriteMovie, removeSelectedMovie, addFavouriteMovie, removeFavouriteMovie }) => {
  
  
  const isFavourite = favouriteMovie.length ? (favouriteMovie.some(fm=>fm.imdbID === selectedMovie.imdbID)) : false;

  const icon = isFavourite ? 
  <Icon onClick={()=> removMovieFromFavourite()} viewBox="0 0 437.775 437.774"  
            
            >
            <path d="M316.722,29.761c66.852,0,121.053,54.202,121.053,121.041c0,110.478-218.893,257.212-218.893,257.212S0,266.569,0,150.801
		         C0,67.584,54.202,29.761,121.041,29.761c40.262,0,75.827,19.745,97.841,49.976C240.899,49.506,276.47,29.761,316.722,29.761z"/>
        </Icon> :

<Icon isLike onClick={()=> addFavouriteMovie(selectedMovie) } viewBox="0 0 437.775 437.774"  
            
>
<path d="M316.722,29.761c66.852,0,121.053,54.202,121.053,121.041c0,110.478-218.893,257.212-218.893,257.212S0,266.569,0,150.801
 C0,67.584,54.202,29.761,121.041,29.761c40.262,0,75.827,19.745,97.841,49.976C240.899,49.506,276.47,29.761,316.722,29.761z"/>
</Icon>;
  
  
  
   
  
  const removMovieFromFavourite = ()=> {
    const movies = favouriteMovie.filter(fm=>fm.imdbID !== selectedMovie.imdbID);
      removeFavouriteMovie(movies);
  }

  console.log(favouriteMovie);
  return (
    <>
      <MovieSelectedContainer>
        <ButtonClose onClick={() => removeSelectedMovie()}>X</ButtonClose>
        <MovieSelectedWrapper>
          <Container>
            <MoviePoster src={selectedMovie.Poster} />
          </Container>
          <MovieDescriptionWrapper>
            <MovieTitle>{selectedMovie.Title}</MovieTitle>          
       

            {icon}

        
            <MovieRating>
              <Star src="star.png" />
              <span>{selectedMovie.imdbRating}/10</span>
            </MovieRating>
            <MovieDetails>
              <DetailKey>Released:</DetailKey>
              <DetailValue>{selectedMovie.Released}</DetailValue>
              <DetailKey>Runtime:</DetailKey>
              <DetailValue>{selectedMovie.Runtime}</DetailValue>
              <DetailKey>Country:</DetailKey>
              <DetailValue>{selectedMovie.Country}</DetailValue>
              <DetailKey>Genre:</DetailKey>
              <DetailValue>{selectedMovie.Genre}</DetailValue>
              <DetailKey>Type:</DetailKey>
              <DetailValue>{selectedMovie.Type}</DetailValue>
            </MovieDetails>
            <DescriptionHeader>Plot</DescriptionHeader>
            <DescriptionText>{selectedMovie.Plot}</DescriptionText>
            <DescriptionHeader>Actors</DescriptionHeader>
            <DescriptionText>{selectedMovie.Actors}</DescriptionText>
            <DescriptionHeader>Awards</DescriptionHeader>
            <DescriptionText>{selectedMovie.Awards}</DescriptionText>
          </MovieDescriptionWrapper>
        </MovieSelectedWrapper>
      </MovieSelectedContainer>
    </>
  );
};

const mapStateToProps = (state) => {
  return {
    selectedMovie: state.movies.selectedMovie,
    favouriteMovie: state.movies.favouriteMovie,
  };
};

const mapDispatchToProps = {
  removeSelectedMovie,
  addFavouriteMovie,
  removeFavouriteMovie
};

export default connect(mapStateToProps, mapDispatchToProps)(MovieSelected);
