import React, { useEffect, useMemo } from "react";
import { connect } from "react-redux";
import Movie from "components/Movie";


import { Container, ReactPaginateWrapper, Wrapper, InnerWrapper, ClosePanel} from "./FavouriteMovieList.css";

const FavouriteMovieList = ({showFavourite, favouriteMovie}) => {
    const moviesList =
    favouriteMovie.length > 0 ?
    favouriteMovie.map((movie) => <Movie movie={movie} key={movie.imdbID} />) : null; 

    const IsMoviesList = moviesList ?  <Container>{moviesList}</Container>: <h3>Favourite movies list is empty</h3>;
    return (
        <>  
            <Wrapper>
                <InnerWrapper>
            <ClosePanel onClick={()=>showFavourite()}>X</ClosePanel>
            {IsMoviesList}
             </InnerWrapper>
            </Wrapper>        
        </>
)
}
const mapStateToProps = (state) => {
    return {
       favouriteMovie: state.movies.favouriteMovie,
    };
  };

  export default connect(mapStateToProps)(FavouriteMovieList);