import styled from "styled-components";
export const Wrapper = styled.div``;

export const Container = styled.div`
  padding: ${({ theme }) => theme.spacing.sm}px 0;
  /* display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 100%; */
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-auto-rows: max-content;
  gap: 10px;
  @media (min-width: 760px) {
    grid-template-columns: repeat(4, 1fr);
    gap: 20px;
  }
  @media (min-width: 1024px) {
    grid-template-columns: repeat(5, 1fr);
  }
`;

export const ReactPaginateWrapper = styled.div`
  display: flex;
  justify-content: center;
  flex-basis: 100%;
  ul {
    list-style-type: none;
    display: flex;
    justify-content: center;
    padding: 0;
    margin-top: ${({ theme }) => `${theme.spacing.xl}px`};
  }

  .pagination li {
    margin: 2px;
    flex-grow: 1;
  }

  .pagination li a {
    padding: 10px;
    background: none;
    cursor: pointer;
    border-radius: 7px;
    border: 2px ${({ theme }) => theme.colors.accent.normal} solid;
    font-family: ${({ theme }) => theme.fonts.heading};
    &:focus {
      outline: none;
    }
    &:hover {
      background: ${({ theme }) => theme.colors.accent.dark};
      border-color: ${({ theme }) => theme.colors.accent.dark};
    }
  }

  li.active a {
    background: ${({ theme }) => theme.colors.accent.normal};

    /* font-weight: 500; */
  }
`;
