import {
  LOADING_STATES,
  CHANGE_SEARCH,
  CHANGE_PAGE,
  CHANGE_MOVIE_ID,
  FETCH_MOVIES_REQUEST,
  FETCH_MOVIES_SUCCESS,
  FETCH_MOVIES_FAILURE,
  FETCH_SELECTED_MOVIE_REQUEST,
  FETCH_SELECTED_MOVIE_SUCCESS,
  FETCH_SELECTED_MOVIE_FAILURE,
  REMOVE_SELECTED_MOVIE,
  ADD_FAVOURITE_MOVIE,
  REMOVE_FAVOURITE_MOVIE
} from "data/constants";

const initialState = {
  loadingState: {},
  movies: [],
  searchMovie: "home",
  page: 1,
  totalResults: 1,
  movieId: "",
  selectedMovie: {},
  favouriteMovie: []
};

export const movies = (state = initialState, action) => {
  const newLoadingState = { ...state.loadingState };
  switch (action.type) {
    case FETCH_MOVIES_REQUEST:
      return {
        ...state,
        loadingState: {
          ...state.loadingState,
          [action.type]: LOADING_STATES.LOADING,
        },
      };
    case FETCH_MOVIES_SUCCESS:
      delete newLoadingState.FETCH_MOVIES_REQUEST;
      return {
        ...state,
        movies: [...action.movies],
        loadingState: newLoadingState,
        totalResults: action.totalResults,
      };
    case FETCH_MOVIES_FAILURE:
      delete newLoadingState.FETCH_MOVIES_REQUEST;
      return {
        ...state,
        movies: {},
        loadingState: newLoadingState,
      };
    case CHANGE_SEARCH:
      return {
        ...state,
        searchMovie: action.movie,
      };
    case CHANGE_PAGE:
      return {
        ...state,
        page: action.page,
      };

    case ADD_FAVOURITE_MOVIE:
      return {
        ...state,
        favouriteMovie: [...state.favouriteMovie, action.favouriteMovie],
      };
    case REMOVE_FAVOURITE_MOVIE:
      return {
        ...state,
        favouriteMovie: [...action.movies],
      };

      case CHANGE_MOVIE_ID:
      return {
        ...state,
        movieId: action.movieId,
      };

    case REMOVE_SELECTED_MOVIE:
      return {
        ...state,
        selectedMovie: {},
      };
    case FETCH_SELECTED_MOVIE_REQUEST:
      return {
        ...state,
        loadingState: {
          ...state.loadingState,
          [action.type]: LOADING_STATES.LOADING,
        },
      };
    case FETCH_SELECTED_MOVIE_SUCCESS:
      delete newLoadingState.FETCH_SELECTED_MOVIE_REQUEST;
      return {
        ...state,
        selectedMovie: action.selectedMovie,
        loadingState: newLoadingState,
      };
    case FETCH_SELECTED_MOVIE_FAILURE:
      delete newLoadingState.FETCH_SELECTED_MOVIE_REQUEST;

      return {
        ...state,
        selectedMovie: {},
        loadingState: newLoadingState,
      };

      

    default:
      return state;
  }
};
