import styled from "styled-components";
 

export const MovieSelectedContainer = styled.div`
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  min-height: 100vh;
  background: ${({ theme }) => theme.colors.accent.opacity};
  padding-top: ${({ theme }) => `${theme.spacing.xl}px`};
  padding-bottom: ${({ theme }) => `${theme.spacing.xl}px`};
  padding-left: ${({ theme }) => `${theme.spacing.s}px`};
  padding-right: ${({ theme }) => `${theme.spacing.s}px`};
  overflow-y: scroll;
  z-index: 999;

  @media (min-width: 1024px) {
    width: 100%;
    left: auto;
    padding-left: 50px;
    box-shadow: ${({ theme }) => theme.colors.background.normal} -5px 25px 25px;
  }

 `;

export const MovieSelectedWrapper = styled.div`
  display: flex;
  flex-wrap: wrap;
  max-width: 960px;
  margin: 0 auto;
`;
export const Container = styled.div`
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  align-content: flex-start;
  flex-basis: 100%;
  @media (min-width: 760px) {
    flex-basis: 40%;
  }
`;

export const MovieDescriptionWrapper = styled.div`
  flex-basis: 100%;
  @media (min-width: 760px) {
    flex-basis: 60%;
    padding-left: 50px;
  }
`;

export const MoviePoster = styled.img`
  display: block;
  width: 80%;
  height: auto;
  border-radius: 14px;
  @media (min-width: 760px) {
    width: 100%;
  }
`;

export const ButtonClose = styled.button`
  background: none;
  color: ${({ theme }) => theme.colors.primary.normal};
  border: 2px solid ${({ theme }) => theme.colors.primary.normal};
  border-radius: 50%;
  margin-left: auto;
  display: block;
  font-size: 20px;
  width: 45px;
  height: 45px;
  &:hover {
    background: ${({ theme }) => theme.colors.accent.dark};
  }
`;

export const MovieTitle = styled.h2`
  text-transform: uppercase;
  font-weight: 500;
`;

export const MovieRating = styled.p`
  font-size: 20.8px;
  font-family: ${({ theme }) => theme.fonts.heading};
  font-weight: 300;
  margin-bottom: ${({ theme }) => `${theme.spacing.xl}px`};
`;

export const Star = styled.img`
  height: 1em;
  padding-right: 5px;
`;
export const MovieDetails = styled.div`
  display: flex;
  flex-wrap: wrap;
  margin-bottom: ${({ theme }) => `${theme.spacing.xl}px`};
`;

export const DetailKey = styled.h3`
  flex-basis: 30%;
  font-family: ${({ theme }) => theme.fonts.heading};
  font-weight: 500;
  text-transform: uppercase;
  font-size: 16.4px;
  margin-bottom: 12px;
`;
export const DetailValue = styled.div`
  flex-basis: 70%;
  font-family: ${({ theme }) => theme.fonts.heading};
  font-weight: 100;
  text-transform: uppercase;
  font-size: 16.4px;
  margin-bottom: 12px;
`;

export const DescriptionHeader = styled.h3`
  font-family: ${({ theme }) => theme.fonts.heading};
  font-weight: 500;
  text-transform: uppercase;
  font-size: 16.4px;
  margin-bottom: 12px;
`;
export const DescriptionText = styled.p`
  margin-bottom: ${({ theme }) => `${theme.spacing.l}px`};
`;
 

export const AddFavouriteMovie = styled.div`
background-image: ${props => props.isLike ? "url('images/like2.png')" : "url('images/like.png')"};
width: 40px;
height: 36px;
display:block;
background-size: cover;
`;

export const Icon = styled.svg`
  flex: none;
   width: 40px;
  height: 40px;
    fill: ${props => !props.isLike ? ({theme}) => theme.colors.accentLight.normal : ({theme}) => theme.colors.primary.normal};
   &:hover {
    fill: ${({ theme }) => theme.colors.background.normal};
  }
`;