import React from "react";
import {
  Container,
  MovieDescription,
  MovieWrapper,
  MoviePosterWrapper,
  MovieYear,
  AddFavouriteMovie
} from "./Movie.css";
import { connect } from "react-redux";

import { changeMovieId, fetchSelectedMovie, fetchMovies, addFavouriteMovie } from "data/actions";

import PropTypes from "prop-types";
const Movie = ({
  movie,
  movieId,
  changeMovieId,
  fetchMovies,
  fetchSelectedMovie,
  MovieShadow,
}) => {
  const posterLink =
    movie.Poster !== "N/A" ? movie.Poster : "images/noposter.jpg";

  return (
    <>
      <Container key={movie.imdbID}>
        <MovieWrapper>
          <MoviePosterWrapper
            
            poster={posterLink}
            onClick={() => fetchSelectedMovie(movie.imdbID)}
          >
            
          </MoviePosterWrapper>
          <MovieDescription>{movie.Title}</MovieDescription>
          <MovieYear>{movie.Year}</MovieYear>
        </MovieWrapper>
      </Container>
    </>
  );
};

const mapDispatchToProps = {
  changeMovieId,
  fetchSelectedMovie,
  fetchMovies,
};

const mapStateToProps = (state) => {
  return {
    movieId: state.movies.movieId,
  };
};

Movie.propTypes = {
  movie: PropTypes.object.isRequired,
};

export default connect(mapStateToProps, mapDispatchToProps)(Movie);
