import styled from "styled-components";
export const Wrapper = styled.div`
   position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  min-height: 100vh;
  background: ${({ theme }) => theme.colors.accent.opacity};
  padding-top: ${({ theme }) => `${theme.spacing.xl}px`};
  padding-bottom: ${({ theme }) => `${theme.spacing.xl}px`};
  padding-left: ${({ theme }) => `${theme.spacing.s}px`};
  padding-right: ${({ theme }) => `${theme.spacing.s}px`};
  overflow-y: scroll;
  z-index: 999;

  @media (min-width: 1024px) {
    width: 100%;
    left: auto;
    padding-left: 50px;
    box-shadow: ${({ theme }) => theme.colors.background.normal} -5px 25px 25px;
  }
`;

export const Container = styled.div`
  padding: ${({ theme }) => theme.spacing.sm}px 0;
  /* display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 100%; */
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-auto-rows: max-content;
  gap: 10px;
  @media (min-width: 760px) {
    grid-template-columns: repeat(4, 1fr);
    gap: 20px;
  }
  @media (min-width: 1024px) {
    grid-template-columns: repeat(5, 1fr);
  }
`;

export const ClosePanel = styled.button`
 background: none;
  color: ${({ theme }) => theme.colors.primary.normal};
  border: 2px solid ${({ theme }) => theme.colors.primary.normal};
  border-radius: 50%;
  margin-left: auto;
  display: block;
  font-size: 20px;
  width: 45px;
  height: 45px;
  &:hover {
    background: ${({ theme }) => theme.colors.accent.dark};
  }

`;

export const InnerWrapper = styled.div`
  max-width: 1200px;
  margin: 0 auto;
`;