import React, { useEffect, useMemo } from "react";
import PropTypes from "prop-types";
import { Container, ReactPaginateWrapper, Wrapper } from "./MovieList.css";
import Movie from "components/Movie";
import Loading from "components/Loading";
import MovieSelected from "components/MovieSelected";
import { connect } from "react-redux";
import { fetchMovies, changeSearch, changePage } from "data/actions";
import ReactPaginate from "react-paginate";

const MovieList = ({
  movies,
  fetchMovies,
  loadingState,
  searchMovie,
  changePage,
  page,
  totalResults,
  selectedMovie,
}) => {
  const moviesList =
    movies.length > 0 &&
    movies.map((movie) => <Movie movie={movie} key={movie.imdbID} />);

  useEffect(() => {
    fetchMovies(searchMovie, page);
  }, [page]);

  const isLoaded = useMemo(() => Object.keys(loadingState).length === 0, [
    loadingState,
  ]);

  const handlePageClick = (data) => {
    const selectedPage = data.selected;
    changePage(selectedPage + 1);
  };

  let paginationElement;

  paginationElement = (
    <ReactPaginateWrapper>
      <ReactPaginate
        previousLabel={"← "}
        nextLabel={" →"}
        breakLabel={<span className="gap">...</span>}
        pageCount={totalResults / 10}
        onPageChange={handlePageClick}
        forcePage={page - 1}
        containerClassName={"pagination"}
        previousLinkClassName={"previous_page"}
        nextLinkClassName={"next_page"}
        disabledClassName={"disabled"}
        activeClassName={"active"}
        pageRangeDisplayed={1}
        marginPagesDisplayed={1}
      />
    </ReactPaginateWrapper>
  );

  const isSelctedMovie = Object.keys(selectedMovie).length !== 0;
  return (
    <>
      {isLoaded ? "" : <Loading />}
      <Wrapper>
        <Container isSelctedMovie={isSelctedMovie}>
          {moviesList ? moviesList : "Brak Wyników"}
        </Container>
        {/* <button onClick={handleChange}>Button</button> */}
        {movies.length && paginationElement}
        {isSelctedMovie && <MovieSelected />}
      </Wrapper>
    </>
  );
};

MovieList.propTypes = {
  movies: PropTypes.array.isRequired,
};

const mapDispatchToProps = {
  fetchMovies,
  changeSearch,
  changePage,
};

const mapStateToProps = (state) => {
  return {
    movies: state.movies.movies,
    searchMovie: state.movies.searchMovie,
    loadingState: state.movies.loadingState,
    page: state.movies.page,
    totalResults: state.movies.totalResults,
    selectedMovie: state.movies.selectedMovie,
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(MovieList);
