import styled from "styled-components";
 
 
export const ChangeThemeButton = styled.div`
background-image: url('images/light.png');
width: 40px;
height: 36px;
display:block;
background-size: cover; 
margin-left: 20px;
`;

export const MenuWrapper = styled.div`
display: flex;
/* justify-content: flex-end; */
margin-bottom: 40px;
`

export const Link = styled.a`
 display: flex;
  align-items: center;
  padding: 5px 10px;
  &:hover {
    color: ${({ theme }) => theme.colors.accent.normal};
  
  }
`;

export const Icon = styled.svg`
  flex: none;
   width: 40px;
  height: 40px;
    fill: ${({ theme }) => theme.colors.accentLight.normal};
  ${Link}:hover  & {
    fill: ${({ theme }) => theme.colors.accent.normal};
  }
`;

export const Label = styled.span`
  display: flex;
  align-items: center;
  line-height: 1.2;   
  margin-left: 5px;
`;
 