import styled from "styled-components";
export const Container = styled.div`
  /* flex-basis: 50%; */
  margin: 0;
`;

export const MovieWrapper = styled.div`
  height: auto;
  position: relative;
`;

export const MoviePosterWrapper = styled.div`
  width: 100%;
  min-height: 240px;
  overflow: hidden;
  border-radius: 15px;
  margin-bottom: 10px;
  background-image: url(${(props) => props.poster});
  background-size: cover;
  background-position: center center;
  background-color: ${({ theme }) => theme.colors.background.normal};
  position: relative;
  cursor: pointer;
  :hover {
    opacity: 0.5;
  }

  @media (min-width: 1024px) {
    height: 320px;
  }
`;

export const MoviePoster = styled.img`
  width: 100%;
  min-height: 100%;
  transition: 0.5s;
  :hover {
    transform: scale(1.2);
  }
`;

export const MovieDescription = styled.h3`
  color: ${({ theme }) => theme.colors.primary.normal};
  margin-bottom: 0;
`;

export const MovieYear = styled.h3`
  color: ${({ theme }) => theme.colors.accent.normal};
  margin-bottom: ${({ theme }) => theme.spacing.l};
`;

export const MovieShadow = styled.p`
  background: ${({ theme }) => theme.colors.background.normal};
  width: 100%;
  height: 100%;
`;

