import {
  FETCH_MOVIES_REQUEST,
  FETCH_MOVIES_SUCCESS,
  FETCH_MOVIES_FAILURE,
  FETCH_SELECTED_MOVIE_REQUEST,
  FETCH_SELECTED_MOVIE_SUCCESS,
  FETCH_SELECTED_MOVIE_FAILURE,
  REMOVE_SELECTED_MOVIE,
  ADD_FAVOURITE_MOVIE,
  REMOVE_FAVOURITE_MOVIE
} from "data/constants";

import API from "data/fetch";

export const fetchMovies = (movie, page) => async (dispatch) => {
  dispatch({
    type: FETCH_MOVIES_REQUEST,
  });
  try {
    // const response = await fetchMovies(movies);
    const response = await API.budget.fetchMovies(movie, page);
    const data = await response.json();
    dispatch({
      type: FETCH_MOVIES_SUCCESS,
      movies: data.Search,
      totalResults: data.totalResults,
    });
  } catch (error) {
    dispatch({
      type: FETCH_MOVIES_FAILURE,
    });
  }
};

