export const fetchSelectedMovie = (id) => async (dispatch) => {
  dispatch({
    type: FETCH_SELECTED_MOVIE_REQUEST,
  });
  try {
    // const response = await fetchMovies(movies);
    const response = await API.budget.fetchSelectedMovie(id);
    const data = await response.json();
    dispatch({
      type: FETCH_SELECTED_MOVIE_SUCCESS,
      selectedMovie: data,
    });
  } catch (error) {
    dispatch({
      type: FETCH_SELECTED_MOVIE_FAILURE,
    });
  }
};

export const changeSearch = (movie) => ({
  type: "CHANGE_SEARCH",
  movie,
});

export const changePage = (page) => ({
  type: "CHANGE_PAGE",
  page,
});

export const changeMovieId = (movieId) => ({
  type: "CHANGE_MOVIE_ID",
  movieId,
});

export const removeSelectedMovie = () => ({
  type: REMOVE_SELECTED_MOVIE,
});


export const addFavouriteMovie = (favouriteMovie) => ({
  type: ADD_FAVOURITE_MOVIE,
  favouriteMovie
});
export const removeFavouriteMovie = (movies) => ({
  type: REMOVE_FAVOURITE_MOVIE,
  movies
});
